const valid = ({fullname, username, email, password, cf_password}) => {
    const err = {}

    if(!fullname) {
        err.fullname = "Hy nhp y  tn ca bn."
    }else if(fullname.length > 25){
        err.fullname = "Tn ti a 25 k t."
    }

    if(!username) {
        err.username = "Hy nhp tn ngi dng."
    }else if(username.replace(/ /g, '').length > 25){
        err.username = "Tn ti a 25 k t."
    }

    if(!email) {
        err.email = "Hy nhp email ca bn."
    }else if(!validateEmail(email)){
        err.email = "nh dng email khng chnh xc."
    }

    if(!password) {
        err.password = "Hy nhp mt khu."
    }else if(password.length < 6){
        err.password = "Mt khu phi trn 6 k t."
    }

    if(password !== cf_password) {
        err.cf_password = "Xc nhn li mt khu khng chnh xc"
    }

    return {
        errMsg: err,
        errLength: Object.keys(err).length
    }
}



function validateEmail(email) {
    // eslint-disable-next-line
    const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
}
  
export default valid